import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Heading extends HtmlElement {
    level: number;
    content: string;
    constructor(content: any, level: any) {
        super();
        this.content = content;
        this.level = level;
    }

    render() {
        return { tag: `h${Math.min(Math.max(this.level, 1), 6)}`, attributes: this.buildAttributes(), events: this.buildEvents(), children: [{ tag: '#text', textContent: this.content }] };
    }

}

