    maxLength: number;
    autoComplete: string;
    onChange: (string: string) => void;
    onInput: (string: string) => void;
    render() {
        let attrs = this.buildAttributes();attrs["type"] = this.type;attrs["value"] = this.value;if (this.placeholder !== null) attrs["placeholder"] = this.placeholder;if (this.disabled) attrs["disabled"] = 'true';if (this.readOnly) attrs["readonly"] = 'true';if (this.required) attrs["required"] = 'true';if (this.name !== null) attrs["name"] = this.name;if ((this.maxLength != null)) attrs["maxlength"] = this.maxLength.toString();if (this.autoComplete !== null) attrs["autocomplete"] = this.autoComplete;let events = this.buildEvents();if (this.onChange !== null) events["change"] = this.onChange;if (this.onInput !== null) events["input"] = this.onInput;return { tag: 'input', attributes: attrs, events: events };
    }

