import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Container extends HtmlElement {
    fluid: boolean;
    maxWidth: string;
    render() {
        let s;
        let attrs = this.buildAttributes();let style = ['margin-right: auto', 'margin-left: auto', 'padding-right: 15px', 'padding-left: 15px', 'width: 100%'];if (this.fluid) {} else if (!!this.maxWidth) {style.push(`max-width: ${this.maxWidth}`);} else {style.push('max-width: 1320px');}let existingStyle = (s = attrs['style']) !== undefined ? s + '; ' : '';attrs["style"] = existingStyle + style.join('; ');return { tag: 'div', attributes: attrs, events: this.buildEvents(), children: this.children.map((c) => c.render()) };
    }

}

