import { BuildContext, Component, HtmlElement, HtmlStyle } from "@equantic/runtime";

export class Flex extends HtmlElement {
    direction: FlexDirection;
    justify: JustifyContent;
    align: AlignItem;
    gap: string;
    wrap: boolean;
    render() {
        let s;
        let attrs = this.buildAttributes();let layoutStyle = new HtmlStyle({ display: 'flex', flexDirection: this.direction, justifyContent: this.justify, alignItems: this.align, gap: this.gap, flexWrap: this.wrap ? 'wrap' : 'noWrap' });let existingStyle = (s = attrs['style']) !== undefined ? s + '; ' : '';attrs["style"] = existingStyle + layoutStyle.toCssString();return { tag: 'div', attributes: attrs, events: this.buildEvents(), children: this.children.map((c) => c.render()) };
    }

}

