import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Text extends HtmlElement {
    content: string;
    paragraph: boolean;
    constructor(content: any) {
        super();
        this.content = content;
    }

    render() {
        return { tag: this.paragraph ? 'p' : 'span', attributes: this.buildAttributes(), events: this.buildEvents(), children: [{ tag: '#text', textContent: this.content }] };
    }

}

