import { BuildContext, Component, HtmlElement } from "@equantic/runtime";
import { List } from "./List";

export class Button extends HtmlElement {
    type: string;
    disabled: boolean;
    text: string;
    render() {
        let attrs = this.buildAttributes();attrs["type"] = this.type;if (this.disabled) attrs["disabled"] = 'true';let children = this.children.some() ? this.children.map((c) => c.render()) : this.text !== null ? [{ tag: '#text', textContent: this.text }] : [];return { tag: 'button', attributes: attrs, events: this.buildEvents(), children: children };
    }

}

